import { newSpecPage } from '@stencil/core/testing';
import { PrestoMutedOverlay } from '../presto-muted-overlay';

describe('presto-muted-overlay', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoMutedOverlay],
      html: `<presto-muted-overlay></presto-muted-overlay>`,
    });
    expect(page.root).toEqualHtml(`
    <presto-muted-overlay>
      <div class="presto-player__muted-overlay"></div>
    </presto-muted-overlay>
    `);
  });
});
